
import React, {Component} from 'react';
import './App.css';
import Contact from './Contact';
import { Route, Link,NavLink, Switch, BrowserRouter as Router } from 'react-router-dom' 



class App extends Component{

  constructor(props){
    super(props);
    this.state = {
      items: [],
      isLoaded: false,

    }
  }

  componentDidMount(){

    fetch('https://public.connectnow.org.uk/applicant-test/')
      .then(res => res.json())
      .then(json => {
        this.setState({
          isLoaded: true,
          items:json,
        })
      });

  }

  render(){

    var {isLoaded, items} = this.state;

    if (!isLoaded) {
      return <div>Loading....</div>;
    }
    else{
      return(
        <div class="container">
          <Router>
          <div class="row header">
            <div class="col-md-12">
            
              <Switch>
                <Link to="/" class="btn btn-info hblcolors btnc">VIDEO GAMES</Link>
              </Switch>
              <Switch>
                <Link to="/Contact" class="btn btn-info hblcolors btnc">CONTACT</Link>
              </Switch>
            
            </div>
          </div>
          <div class="row">
            <Route path="/" exact>
              <div class="col-md-2">
                <h3>Filter Results</h3>
                <form>
                  <div class="form-group">
                    <label class="form-label">Name</label>
                    <input class="form-control" type="text" placeholder="Type string" />
                  </div>
                  <div class="form-group">
                    <label class="form-label">Minimum Score</label>
                    <input class="form-control" type="text" placeholder="1 to 10"/>
                  </div>

                  <div class="form-group">
                    <label class="form-label">Order By</label>
                    <select class="form-select" aria-label="Default select example">
                      <option value="3">Release date</option>
                      <option value="1">Score</option>
                      <option value="2">Name</option>
                    </select>
                  </div>
                </form>
              </div>
              <div class="col-md-10">
              {items.map(item =>(
                <div class="card cardstyle cardgap">
                  <div class="card-body" key={item.id}>
                    <h5 class="card-title hblcolors">{item.name} <span class="badge bg-secondary hblcolors rht">{item.rating}</span></h5>
                    <h6 class="card-subtitle mb-2 text-muted hblcolors">Release date: {item.first_release_date}</h6>
                    <p class="card-text">{item.summary}</p>
                  </div>
                </div>
                ))}
              </div>
            </Route>
            <Route path="/Contact">
              <div class="col-md-2">
                
              </div>

              <div class="col-md-10">
                <h3 class="hblcolors">Get In Touch</h3>
                <p>Trysail transomfurl sea legs scallywag jack ketch chanddler miszzanmast reef sails skysail. Shiver me thimber loot bucko</p>
                <form>
                  <div class="form-group">
                    <label class="form-label">Name</label>
                    <input type="text" name="name" class="form-control inputbg" />
                  </div>
                  <div class="form-group">
                    <label class="form-label">Email</label>
                    <input type="text" name="name" class="form-control inputbg" />
                  </div>
                  <div class="form-group">
                    <label class="form-label">Message</label>
                    <textarea type="text" rows="5" name="name" class="form-control inputbg"> </textarea>
                  </div>
                  <br/>
                  <div class="form-group">
                    <input type="submit" value="Send" class="btn btn-primary btnr"/>
                  </div>
                </form>
              </div>
            </Route>
          </div>
          </Router>
        </div>
      );
    }
  }
}

export default App;
